<template>
  <view class="container">
    <!-- 搜索框 -->
    <view class="search-box" @click="goSearch">
      <uni-icons type="search" size="18"></uni-icons>
      <text>搜索商家名称</text>
    </view>

    <!-- 商家列表 -->
    <scroll-view scroll-y class="shop-list">
      <view 
        v-for="shop in shopList" 
        :key="shop.id"
        class="shop-item"
        @click="goShopDetail(shop.id)"
      >
        <image :src="shop.logo" class="shop-logo"></image>
        <view class="shop-info">
          <text class="shop-name">{{ shop.name }}</text>
          <uni-rate :value="shop.rating" size="14"></uni-rate>
          <text class="distance">{{ shop.distance }}km</text>
        </view>
      </view>

      <!-- 加载状态 -->
      <uni-load-more :status="loadingStatus"></uni-load-more>
    </scroll-view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      shopList: [],
      loadingStatus: 'loading'
    }
  },
  onLoad() {
    this.loadShops()
  },
  methods: {
    async loadShops() {
      try {
        const res = await uni.request({
          url: '/api/shop/list',
          method: 'GET'
        })
        this.shopList = res.data
        this.loadingStatus = 'more'
      } catch (e) {
        this.loadingStatus = 'error'
      }
    },
    goShopDetail(shopId) {
      uni.navigateTo({
        url: `/pages/shop/shop?id=${shopId}`
      })
    },
    // goSearch() {
    //   uni.navigateTo({
    //     url: '/pages/search/search'
    //   })
    // }
  }
}
</script>

